module.exports = "import { ModalContentProps, openModal } from 'ssenkit.modal';\nimport * as React from 'react';\nimport * as styles from './openModal.sample.module.scss';\n\nconst Content: React.StatelessComponent<ModalContentProps> = ({closeModal}) => {\n  return (\n    <div className={styles.main}>\n      <h1>HELLO?</h1>\n      <button onClick={() => closeModal && closeModal()}>Close Modal</button>\n    </div>\n  );\n};\n\nexport interface Props {\n\n}\n\ninterface InternalProps {\n\n}\n\ninterface State {\n}\n\nclass Component extends React.PureComponent<Props & InternalProps, State> {\n  static displayName: string = 'openModal.sample';\n  \n  render() {\n    return (\n      <div>\n        <button onClick={this.open}>Open Modal</button>\n      </div>\n    );\n  }\n  \n  open = () => {\n    openModal(<Content/>);\n  };\n}\n\nexport default Component as React.ComponentType<Props>;"