module.exports = "import { FromTo, FromToDateTimeDropDownSelector } from 'ssenkit.date-select';\nimport * as moment from 'moment';\nimport * as React from 'react';\n\ninterface State {\n  fromTo: FromTo;\n}\n\nexport default class extends React.PureComponent<{}, State> {\n  state: State = {\n    fromTo: {\n      from: moment().subtract(4, 'days').startOf('day').toDate(),\n      to: new Date(),\n    },\n  };\n  \n  render() {\n    return (\n      <div>\n        <FromToDateTimeDropDownSelector fromTo={this.state.fromTo} onChange={this.onChange}/>\n        <span>Selected: {moment(this.state.fromTo.from).format('YYYY-MM-DD HH:mm:ss')} ~ {moment(this.state.fromTo.to).format('YYYY-MM-DD HH:mm:ss')}</span>\n      </div>\n    );\n  }\n  \n  onChange = (fromTo: FromTo) => {\n    this.setState({fromTo});\n  };\n}"