module.exports = "import * as Recontext from 'recontext';\n\nmodule test {\n  interface State {\n    x: number;\n    y: number;\n    z: number;\n  }\n  \n  interface Actions {\n    updateX: (x: number) => void;\n    updateY: (y: number) => void;\n  }\n  \n  export type Store = State & Actions;\n  \n  export const createStore: Recontext.CreateStore<State, Actions> = Recontext.createStore<State, Actions>(\n    {\n      x: 1,\n      y: 2,\n      z: 3,\n    },\n    setState => ({\n      updateX: ({y}) => x => {\n        setState({\n          x,\n          z: x + y,\n        });\n      },\n      updateY: ({x}) => y => {\n        setState({\n          y,\n          z: x + y,\n        });\n      },\n    }),\n  );\n}\n\nexport default test;"