module.exports = "import * as React from 'react';\nimport Modal, { ModalContentProps } from 'ssenkit.modal';\nimport * as styles from './Modal.sample.module.scss';\n\nconst Content: React.StatelessComponent<ModalContentProps> = ({closeModal}: ModalContentProps) => {\n  return (\n    <div className={styles.main}>\n      <h1>HELLO?</h1>\n      <button onClick={() => closeModal && closeModal()}>Close Modal</button>\n    </div>\n  );\n};\n\nexport interface Props {\n\n}\n\ninterface InternalProps {\n\n}\n\ninterface State {\n  modal: React.ReactElement<{}> | null,\n}\n\nclass Component extends React.PureComponent<Props & InternalProps, State> {\n  static displayName: string = 'Modal.sample';\n  \n  state: State = {\n    modal: null,\n  };\n  \n  render() {\n    return (\n      <div>\n        <button onClick={this.openModal}>Open Modal</button>\n        {this.state.modal}\n      </div>\n    );\n  }\n  \n  openModal = () => {\n    this.setState({\n      modal: (\n        <Modal onClose={this.closeModal}>\n          <Content/>\n        </Modal>\n      ),\n    });\n  };\n  \n  closeModal = () => {\n    this.setState({\n      modal: null,\n    });\n  };\n}\n\nexport default Component as React.ComponentType<Props>;"