module.exports = "import { MultipleAutoCompleteTextInput } from 'ssenkit.autocomplete-text-input';\nimport * as React from 'react';\nimport './MultipleAutoCompleteTextInput.sample.scss';\n\nconst availableTags: string[] = [\n  'ActionScript',\n  'AppleScript',\n  'Asp',\n  'BASIC',\n  'C',\n  'C++',\n  'Clojure',\n  'COBOL',\n  'ColdFusion',\n  'Erlang',\n  'Fortran',\n  'Groovy',\n  'Haskell',\n  'Java',\n  'JavaScript',\n  'Lisp',\n  'Perl',\n  'PHP',\n  'Python',\n  'Ruby',\n  'Scala',\n  'Scheme',\n];\n\nfunction split(val: string): string[] {\n  return val.split(/,\\s*/);\n}\n\nfunction extractLast(term: string): string {\n  return split(term).pop() || '';\n}\n\nfunction source(request: {term: string}, response: (data: object) => void) {\n  response($.ui.autocomplete.filter(availableTags, extractLast(request.term)));\n}\n\ninterface State {\n  text: string;\n}\n\nexport default class extends React.PureComponent<{}, State> {\n  state: State = {\n    text: '',\n  };\n  \n  render() {\n    return (\n      <div>\n        <MultipleAutoCompleteTextInput menuClassName=\"MultipleAutoCompleteTextInputSample-menu\"\n                                       className=\"MultipleAutoCompleteTextInputSample-input\"\n                                       value={this.state.text}\n                                       minLength={0}\n                                       source={source}\n                                       onChange={this.onChange}\n                                       onSubmit={this.onChange}/>\n        <p>{this.state.text}</p>\n      </div>\n    );\n  }\n  \n  onChange = (text: string) => {\n    this.setState({text});\n  };\n}"